.card-content {
  position: fixed;
  top: 0%;
  overflow-y: scroll;
  max-height: inherit;
  margin: auto;
  width: 100vw;
  height: 100vh;
  background-color: #eeeeee;
}
.card-content::-webkit-scrollbar {
  display: none;
}
.card-content .card-header {
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0%;
  z-index: 100;
  padding: 10px 15px;
  background-color: white;
}
.card-content .card-header__title {
  font-size: 3rem;
}
.card-content .card-header__close {
  font-size: 3rem;
  padding: 5px;
}
@media only screen and (min-width: 768px) {
  .card-content .card-body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 5%;
  }
}
.card-content .card-body hr {
  background-color: #000000;
  border: none;
  height: 1px;
}
.card-content .card-body__img {
  width: 100%;
  max-width: 500px;
  border: 1px solid #7e7e7e;
  object-fit: contain;
  place-self: center;
}
.card-content .card-body__info {
  place-self: center;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  padding: 15px;
  font-size: 1.5rem;
}
.card-content .card-body__border-title {
  font-size: 3rem;
}
.card-content .card-body__border-info {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 15px;
  gap: 15px;
}
.card-content .card-body__border-info p {
  font-size: 1.5rem;
  border: 1px solid #7e7e7e;
  border-radius: 10px;
  padding: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.card-content .card-body__border-info p:hover {
  cursor: pointer;
  background-color: #7e7e7e;
  color: #ffffff;
}

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "Roboto", sans-serif;
}
html body {
  max-width: 100vw;
  min-width: 320px;
}
@media only screen and (max-width: 375px) {
  html body::-webkit-scrollbar {
    display: none;
  }
}
html .modal-backdrop {
  position: absolute;
  top: 0%;
  left: 0%;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.623);
  overflow: hidden;
  z-index: 100;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  z-index: 0;
}
header .header__title {
  font-size: 3rem;
}
header .header__theme-icon {
  font-size: 1.5rem;
  color: #7e7e7e;
  border: 1px solid #7e7e7e;
  padding: 5px 10px;
  border-radius: 10px;
}
header .header__theme-icon:hover {
  background-color: #7e7e7e;
  color: #ffffff;
}

main {
  background-color: #ededed;
  min-height: 100vh;
  padding: 10px;
  z-index: 0;
}
main .main__section-search {
  display: flex;
  padding: 10px 0;
  margin: 0 5%;
}
main .main__section-search-input {
  border-radius: 15px;
  overflow: hidden;
  font-size: 1.5rem;
  border: none;
  padding: 10px;
  width: 100%;
}
main .main__section-select {
  margin: 0 5% 30px;
}
main .main__section-select-item {
  font-size: 1.5rem;
  border: none;
  border-bottom: #000000 solid 1px;
  background-color: transparent;
  color: #7e7e7e;
  padding: 5px;
}
main .countrys-container ul {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  margin: 0 5%;
}
main .countrys-container ul .country {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 325px;
  border-radius: 15px;
  overflow: hidden;
  background-color: white;
}
main .countrys-container ul .country__text-container {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  padding: 10px;
}
main .countrys-container ul .country__text-container h3 {
  font-size: 3rem;
}
main .countrys-container ul .country__text-container p {
  font-size: 1.5rem;
}
main .countrys-container ul .country img {
  height: 180px;
}
main .countrys-container ul .country .btn-more-details {
  font-size: 1.5rem;
  color: #000000;
  border: 1px solid #7e7e7e;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  border-radius: 10px;
  margin: 0 0 10px 10px;
}
main .countrys-container ul .country .btn-more-details:hover {
  background-color: #7e7e7e;
  color: #ffffff;
  cursor: pointer;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
}
.loading p {
  font-size: 3rem;
  font-weight: 700;
}
.loading i {
  font-size: 3rem;
  margin: 0%;
}

.dark-theme header {
  background-color: #384b5c;
  color: #ffffff;
}
.dark-theme header .header__theme-icon {
  color: #ffffff;
  border-color: #ffffff;
}
.dark-theme main {
  background-color: #202c37;
  color: #ffffff;
}
.dark-theme main #input::-webkit-input-placeholder {
  color: #ffffff;
}
.dark-theme main #input::placeholder {
  color: #ffffff;
}
.dark-theme main #input,
.dark-theme main #select {
  color: #ffffff;
  background-color: #384b5c;
  border-bottom: 1px solid #7e7e7e;
}
.dark-theme main .countrys-container ul .country {
  background-color: #384b5c;
}
.dark-theme main .countrys-container ul .country .btn-more-details {
  color: #ffffff;
}
.dark-theme main .countrys-container ul .country .btn-more-details:hover {
  background-color: #eeeeee;
  color: #000000;
}
.dark-theme .card-header {
  background-color: #384b5c;
}
.dark-theme .card-body,
.dark-theme .card-content {
  background-color: #202c37;
  color: #ffffff;
}
.dark-theme .card-body__border-info p:hover {
  cursor: pointer;
  background-color: #eeeeee;
  color: #000000;
}
.dark-theme .card-body hr {
  background-color: #ffffff;
}
